@import "../../_globalColor"; // Assuming this path is correct and the file exists

.small-dark {
  color: $textColorDark !important;
}

.blog-container {
  border-radius: 10px;
  color: $blogCardContainerColor;
  background-color: $lightBackground1;
  box-shadow: 0 0 36px $lightBoxShadow;
  cursor: pointer;
  height: 200px; // Base height for larger screens
}
.light-mode {
  background: transparent;
  color: white !important;
  transition: "0.1s";
}

.blog-title {
  color: $blogCardTitleColor;
  font-size: 1.375rem; // Base font size
  line-height: 24px;
  font-weight: 700;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 14px;
  margin-top: 32px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.small {
  font-size: 1.063rem; // Base font size
  font-weight: 400;
  line-height: 1.5rem;
  color: $cardSubtitle;
  padding: 0px 20px; // Base padding
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  margin: 0px;
}

.go-corner {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 32px; // Base width
  height: 32px; // Base height
  overflow: hidden;
  top: 0;
  right: 0;
  background-color: $buttonColor;
  border-radius: 0 4px 0 32px;
}

.go-arrow {
  margin-top: -4px;
  margin-right: -4px;
  color: $textColorDark;
  font-family: courier, sans;
}

.blog-card {
  display: block;
  position: relative;
  background-color: transparent;
  border-radius: 4px;
  text-decoration: none;
  z-index: 0;
  overflow: hidden;
  height: 100%;
}

.blog-card::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -16px;
  right: -16px;
  background: $buttonColor;
  height: 32px;
  width: 32px;
  border-radius: 32px;
  transform: scale(1);
  transform-origin: 50% 50%;
  transition: transform 0.4s ease-out; // Base transition for hover effect
}

.blog-card:hover::before {
  transform: scale(40); // Default scale for larger screens
}

.blog-card:hover > .small {
  transition: all 0.3s ease-out;
  color: $blogCardHoverSmallColor;
}

.blog-card:hover > h3 {
  transition: all 0.3s ease-out;
  color: $darkBoxShadow2;
}

// Removed the old complex media queries for blog-card:hover::before
// They are now consolidated into the new breakpoints below.

@media screen and (max-width: 480px) and (min-width: 300px) {
  .blog-title {
    font-size: 1.125rem;
  }
}
.blog-card-shadow {
  box-shadow: 1px 0px 20px $darkBoxShadow2;
}


/* --- Responsive Adjustments using Media Queries (New Additions/Consolidations) --- */

// Tablet devices (max-width: 1024px)
@media (max-width: 1024px) {
  .blog-container {
    height: 180px; // Slightly reduce height for tablets
  }

  .blog-title {
    font-size: 1.2rem; // Adjust font size for tablets
    margin-top: 25px; // Adjust top margin
    margin-left: 15px;
    margin-right: 15px;
  }

  .small {
    font-size: 0.95rem; // Adjust font size for tablets
    padding: 0px 15px; // Adjust padding
  }

  .go-corner {
    width: 28px; // Slightly smaller corner
    height: 28px;
    border-radius: 0 4px 0 28px;
  }

  .blog-card:hover::before {
    transform: scale(35); // Adjust hover scale for tablets
    transition: transform 0.42s ease-out;
  }
}

// Mobile devices (max-width: 767px)
@media (max-width: 767px) {
  .blog-container {
    height: 160px; // Further reduce height for mobile
  }

  .blog-title {
    font-size: 1.1rem; // Adjust font size for mobile
    margin-top: 20px; // Adjust top margin
    margin-left: 10px;
    margin-right: 10px;
  }

  .small {
    font-size: 0.85rem; // Adjust font size for mobile
    padding: 0px 10px; // Adjust padding
  }

  .go-corner {
    width: 24px; // Smaller corner for mobile
    height: 24px;
    border-radius: 0 4px 0 24px;
  }

  .go-arrow {
    margin-top: -3px; // Adjust arrow position
    margin-right: -3px;
  }

  .blog-card:hover::before {
    transform: scale(30); // Adjust hover scale for mobile
    transition: transform 0.4s ease-out;
  }
}

// Very small mobile devices (max-width: 480px) - This specifically targets smaller phones
// Note: Your original SCSS already had a media query for blog-title at 480px,
// I've kept it and added other adjustments here.
@media (max-width: 480px) {
  .blog-container {
    height: 150px; // Even smaller height for very small screens
  }

  .blog-title {
    font-size: 1.0rem; // Further adjust font size
    margin-top: 15px;
    margin-bottom: 10px;
  }

  .small {
    font-size: 0.8rem; // Further adjust font size
    padding: 0px 8px;
    -webkit-line-clamp: 3; // Maybe show one less line on very small screens
  }

  .go-corner {
    width: 20px; // Even smaller corner
    height: 20px;
    border-radius: 0 3px 0 20px;
  }

  .go-arrow {
    font-size: 0.8rem; // Smaller arrow text
  }

  .blog-card:hover::before {
    transform: scale(25); // Adjust hover scale for very small screens
    transition: transform 0.38s ease-out;
  }
}