@import "../../_globalColor"; // Assuming this path is correct and the file exists

.main {
  width: 100%; // Base width
  padding: 20px 30px; // Base padding
  margin: 0px auto; // Centers the main content horizontally
  z-index: 11;
}
.light-mode {
  background: transparent;
  color: white !important;
  transition: "0.1s";
}
.greet-main {
  width: 90%; // Base width for the greeting section
  padding: 20px 10px; // Base padding
  margin: 0px auto; // Centers the greeting section horizontally
  margin-top: 2rem; // Base top margin
}

.greeting-main {
  display: flex; // Base display for larger screens
}

.subTitle {
  color: $subTitle !important;
}

.greeting-main > * {
  flex: 1;
  margin-bottom: 30px; // Base bottom margin for flex items
}

.empty-div {
  margin-bottom: 40px; // Base bottom margin
}

.button-greeting-div {
  display: flex; // Base display for buttons
  margin-top: 40px; // Base top margin
}
.download-link-button {
  text-decoration: none;
}

.greeting-text {
  font-size: 70px; // Base font size
  line-height: 1.1;
  color: #c9c9c9 !important;
}

.greeting-text-p {
  font-size: 30px; // Base font size
  line-height: 40px; // Base line-height
}

.greeting-image-div > img {
  max-width: 100%;
  height: auto;
}

.wave-emoji {
  animation-duration: 1.8s;
  animation-iteration-count: infinite;
  animation-name: wave;
  display: inline-block;
  transform-origin: 70% 70%;
}

@keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

/* --- Existing Media Queries (UNCHANGED as per request) --- */
@media (max-width: 1380px) {
  .greeting-text {
    font-size: 50px;
  }
  .greeting-text-p {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .button-greeting-div {
    justify-content: space-around;
  }
  .greeting-text {
    font-size: 30px;
    text-align: center;
  }
  .greeting-text-p {
    font-size: 16px;
    line-height: normal;
    text-align: center;
  }
  .greeting-main {
    display: block; // Stack sections vertically
  }
  .greeting-image-div {
    height: 256px; // Fixed height for image div
  }
  // When stacking, ensure content is centered within the flex item
  .greeting-main > * {
    text-align: center;
    margin-bottom: 20px; /* Adjust margin for stacked items */
  }
}

@media (max-width: 320px) {
  .main {
    width: auto; // Allow width to adjust automatically
  }
}

/* --- New Media Queries for Responsiveness --- */

// Tablet devices (max-width: 1024px)
@media (max-width: 1024px) {
  .main {
    padding: 15px 20px; // Adjust padding
  }

  .greet-main {
    width: 95%; // Slightly increase width for tablets
    padding: 15px 10px; // Adjust padding
    margin-top: 1.8rem; // Adjust top margin
  }

  .greeting-text {
    font-size: 45px; // Adjust font size for tablets (between 1380px and 768px)
  }

  .greeting-text-p {
    font-size: 18px; // Adjust font size for tablets
    line-height: 28px; // Adjust line-height
  }

  .button-greeting-div {
    margin-top: 30px; // Adjust top margin for buttons
  }
}

// Mobile devices (max-width: 480px)
@media (max-width: 480px) {
  .main {
    padding: 10px 15px; // Further adjust padding
  }

  .greet-main {
    width: 98%; // Further increase width for mobile
    padding: 10px 5px; // Further adjust padding
    margin-top: 1.5rem; // Further adjust top margin
  }

  .greeting-text {
    font-size: 28px; // Further reduce font size for mobile
    line-height: 1.2; // Adjust line-height
  }

  .greeting-text-p {
    font-size: 15px; // Further reduce font size for mobile
    line-height: 24px; // Further adjust line-height
  }

  .button-greeting-div {
    flex-direction: column; // Stack buttons vertically on very small screens
    align-items: center; // Center stacked buttons
    margin-top: 20px; // Reduce top margin
    gap: 10px; // Add gap between stacked buttons
  }

  .greeting-main > * {
    margin-bottom: 15px; // Reduce bottom margin for stacked items
  }

  .empty-div {
    margin-bottom: 20px; // Reduce bottom margin for empty div
  }

  .greeting-image-div {
    height: 200px; // Further reduce image div height
  }
}

// Very small mobile devices (max-width: 375px) - Overrides 320px for font size
@media (max-width: 375px) {
  .greeting-text {
    font-size: 24px; // Smallest font size for heading
  }

  .greeting-text-p {
    font-size: 14px; // Smallest font size for paragraph
    line-height: 22px;
  }

  .button-greeting-div {
    margin-top: 15px; // Smallest top margin for buttons
    gap: 8px; // Smallest gap
  }

  .greeting-image-div {
    height: 180px; // Smallest image div height
  }
}