@import "../../_globalColor";

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box;
}

.talk-header-title {
  color: #c9c9c9 !important;
  font-size: 56px;
  font-weight: 400;
  line-height: 0px;
  text-align: center;
}

.light-mode {
  background: transparent;
  color: white !important;
  transition: 0.1s;
}

.subTitle {
  color: $subTitle;
  text-align: center;
}

.talks-content-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  max-width: 1200px;
  margin-top: 40px;
  flex-wrap: wrap;
  gap: 20px;
}

.talk-cards-wrapper {
  flex-grow: 1;
  flex-basis: 0;
  min-width: 290px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  gap: 1rem 1rem;
  justify-items: center;
}

.FloatingSendButton {
}

@media (max-width: 768px) {
  .talk-header-title {
    font-size: 30px;
  }
  .talk-header-subtitle {
    font-size: 16px;
    line-height: normal;
  }

  .talks-content-container {
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }

  .talk-cards-wrapper {
    width: 100%;
    grid-template-columns: 1fr;
    margin-right: 0;
  }
}

@media (max-width: 1024px) {
  .main {
    padding: 15px;
  }

  .talk-header-title {
    font-size: 40px;
  }

  .talks-content-container {
    margin-top: 30px;
    gap: 15px;
  }

  .talk-cards-wrapper {
    min-width: 250px;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 0.8rem 0.8rem;
  }
}

@media (max-width: 480px) {
  .main {
    padding: 10px;
  }

  .talk-header-title {
    font-size: 24px;
  }

  .subTitle {
    font-size: 0.9rem;
  }

  .talks-content-container {
    margin-top: 20px;
    gap: 20px;
  }

  .talk-cards-wrapper {
    min-width: 200px;
    grid-template-columns: 1fr;
    gap: 0.7rem 0.7rem;
  }
}

@media (max-width: 375px) {
  .talk-header-title {
    font-size: 20px;
  }

  .subTitle {
    font-size: 0.85rem;
  }

  .talks-content-container {
    margin-top: 15px;
    gap: 15px;
  }

  .talk-cards-wrapper {
    min-width: 180px;
    gap: 0.6rem 0.6rem;
  }
}