@import "../../_globalColor";

.card-image {
  max-width: 100%;
  height: auto;
  width: 250px;
}
.light-mode {
  background: transparent;
  color: white !important;
  transition: "0.1s";
}
.card-title {
  color: $titleColor;
  font-size: 22px;
  line-height: 24px;
  font-weight: 700;
  margin: 15px 0 0 0;
}
.card-subtitle {
  color: $cardSubtitle;
  font-size: 17px;
  line-height: 1.5rem;
}
.project-card {
  display: flex;
  flex-direction: column;
  background-color: $lightBackground2;
  padding: 1.5rem;
  border-radius: 10px;
  border: 1px solid $lightBorder1;
  justify-content: center;
}
.project-card-light {
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.project-card-dark {
  box-shadow: 0px 10px 30px -15px $darkBoxShadow;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.25s ease;
}

.project-detail {
  text-align: center;
}
.project-card-light:hover {
  box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
}
.project-card-dark:hover {
  box-shadow: 0px 20px 30px -10px $darkBoxShadow;
}
.project-image {
  position: relative;
  height: 250px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem 1rem;
}

.project-card-footer span.project-tag {
  background: $buttonColor;
  color: $lightBackground3;
  vertical-align: middle;
  align-items: center;
  border-radius: 4px;
  display: inline-flex;
  font-size: 0.75rem;
  height: 2em;
  justify-content: center;
  white-space: nowrap;
  line-height: 1.5;
  margin: 0 0.5rem 0.5rem 0;
  padding: 0 0.75em;
  cursor: pointer;
  transition: 0.2s ease-in;
}

@media (max-width: 768px) {
  .project-subtitle {
    font-size: 16px;
    text-align: center;
  }
}

@media (max-width: 1024px) {
  .card-image {
    width: 200px;
  }
  .card-title {
    font-size: 20px;
  }
  .card-subtitle {
    font-size: 16px;
  }
  .project-card {
    padding: 1.25rem;
  }
  .project-image {
    height: 200px;
  }
  .projects-container {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 0.8rem 0.8rem;
  }
  .project-card-footer span.project-tag {
    font-size: 0.7rem;
    height: 1.8em;
    margin: 0 0.4rem 0.4rem 0;
    padding: 0 0.6em;
  }
}

@media (max-width: 767px) {
  .card-image {
    width: 180px;
  }
  .card-title {
    font-size: 18px;
  }
  .card-subtitle {
    font-size: 15px;
  }
  .project-card {
    padding: 1rem;
  }
  .project-image {
    height: 180px;
  }
  .projects-container {
    grid-template-columns: 1fr;
    gap: 0.7rem 0.7rem;
  }
  .project-card-footer span.project-tag {
    font-size: 0.65rem;
    height: 1.6em;
    margin: 0 0.3rem 0.3rem 0;
    padding: 0 0.5em;
  }
}

@media (max-width: 480px) {
  .card-image {
    width: 150px;
  }
  .card-title {
    font-size: 16px;
  }
  .card-subtitle {
    font-size: 14px;
  }
  .project-card {
    padding: 0.8rem;
  }
  .project-image {
    height: 150px;
  }
  .project-card-footer span.project-tag {
    font-size: 0.6rem;
    height: 1.4em;
    margin: 0 0.2rem 0.2rem 0;
    padding: 0 0.4em;
  }
}

@media (max-width: 375px) {
  .card-image {
    width: 120px;
  }
  .card-title {
    font-size: 14px;
  }
  .card-subtitle {
    font-size: 13px;
  }
  .project-card {
    padding: 0.6rem;
  }
  .project-image {
    height: 120px;
  }
  .project-card-footer span.project-tag {
    font-size: 0.55rem;
    height: 1.2em;
    margin: 0 0.15rem 0.15rem 0;
    padding: 0 0.3em;
  }
}