@import "../../_globalColor"; // Assuming this path is correct and the file exists

.main-button {
  background-color: $buttonColor;
  border: solid 1px $buttonColor;
  color: $textColorDark;
  font-weight: 700;
  width: max-content; // Base width behavior
  padding: 13px 22px; // Base padding
  margin-right: 50px; // Base margin for standalone buttons or first in a row
  text-transform: uppercase;
  border-radius: 6px;
  text-align: center;
  text-decoration: none;
  display: block; // Ensures it takes full width if needed, but width: max-content overrides this for its own content
  margin-top: 20px;
  font-size: 1rem; // Base font size
  cursor: pointer;
  transition: all 0.3s ease-in-out 0s;
}
.main-button:hover {
  background-color: $darkBoxShadow2;
  color: $buttonColor;
  transition: all 0.3s ease 0s;
  transform: translateY(-3px);
}
.light-mode.main-button {
  &:hover {
    color: #000 !important; // Example: white text on hover in light mode
  }
}
.project-button {
  display: flex;
  justify-content: center; // Centers buttons horizontally
  margin-top: 20px;
  flex-wrap: wrap; // Allow buttons to wrap to next line on smaller screens
  gap: 10px; // Add a small gap between wrapped buttons
}
.project-button > .main-button {
  margin-right: 0 !important; // Ensures buttons within .project-button don't have the 50px right margin
  margin-bottom: 10px; // Add some vertical spacing when wrapped
}

/* --- Responsive Adjustments using Media Queries (New Additions) --- */

// Tablet devices (max-width: 1024px)
@media (max-width: 1024px) {
  .main-button {
    font-size: 0.95rem; // Slightly smaller font for tablets
    padding: 12px 20px; // Slightly less padding
    margin-right: 30px; // Reduce right margin for standalone buttons on tablets
  }

  // No specific changes needed for .project-button here, flex-wrap and gap handle it
}

// Mobile devices (max-width: 767px)
@media (max-width: 767px) {
  .main-button {
    font-size: 0.9rem; // Smaller font for mobile
    padding: 10px 16px; // Reduced padding
    margin-right: 0px; // Ensure no right margin on mobile for standalone buttons
    width: 100%; // Make buttons take full width on mobile for better touch target
    display: block; // Ensure block display for full width
  }

  .project-button {
    flex-direction: column; // Stack buttons vertically on mobile
    align-items: center; // Center stacked buttons
  }
  .project-button > .main-button {
    width: 100%; // Ensure buttons inside project-button also take full width
    margin-bottom: 15px; // Increase vertical spacing between stacked buttons
  }
}

// Very small mobile devices (max-width: 480px)
@media (max-width: 480px) {
  .main-button {
    font-size: 0.85rem; // Even smaller font for very small screens
    padding: 8px 12px; // Even less padding
  }
  .project-button > .main-button {
    margin-bottom: 10px; // Adjust vertical spacing
  }
}

// Original 320px media query (kept for specific font size adjustment)
@media (max-width: 320px) {
  .main-button {
    font-size: 0.75rem; // Original very small font size
  }
}