@import "../../_globalColor"; // Assuming this path is correct and the file exists

.education-card {
  display: flex;
  flex-direction: row; // Base direction for larger screens
}
.light-mode {
  background: transparent;
  color: white !important;
  transition: "0.1s";
}

.education-card:hover + .education-card-border {
  border-color: $educationCardBorder;
  width: 100%;
  -webkit-transition: width 0.6s ease-in-out;
  -moz-transition: width 0.6s ease-in-out;
  -o-transition: width 0.6s ease-in-out;
  transition: width 0.6s ease-in-out;
}

.education-card-border {
  height: 30px;
  width: 80%; // Base width for the border
  border-top: 2px solid $lightBorder1;
  margin-bottom: 20px;
  -webkit-transition: width 0.6s ease-in-out;
  -moz-transition: width 0.6s ease-in-out;
  -o-transition: width 0.6s ease-in-out;
  transition: width 0.6s ease-in-out;
}

.education-head {
  display: flex;
  flex-direction: row; // Base direction for larger screens
}

.education-text-school {
  color: #c0c0c0 !important;
  margin: 0;
  font-size: 28px; // Base font size
  font-weight: 700;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  height: 27px;
}

.education-roundedimg {
  object-fit: cover;
  margin-right: 2rem; // Base margin
  width: 6rem; // Base width
  height: 6rem; // Base height
  box-shadow: 0 0.5rem 1rem $lightBoxShadowDarker;
  border-radius: 50%;
}

.education-card-right {
  max-width: 70%; // Base max-width
}

.education-text-subHeader {
  color: #c0c0c0 ;
  font-weight: 700;
  font-size: 19px; // Base font size
  margin: 0px;
  padding-top: 0.8rem;
  line-height: normal;
}

.education-text-duration {
  color: #c0c0c0 !important;
  font-size: 19px; // Base font size
  margin: 0px;
  padding-top: 0.5rem;
}

.education-text-desc {
  color: #c0c0c0 !important;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.education-text-bullets > ul {
  line-height: 170%;
  margin-top: 0.5rem;
}

/* --- Existing Media Queries (UNCHANGED as per request) --- */

@media (max-width: 1380px) {
  .education-card {
    flex-direction: column;
    padding-bottom: 10px;
  }
  .education-card-right {
    max-width: 90%;
  }
  .education-card-border {
    width: 90%;
  }
  .education-text-subHeader {
    padding-top: 0.5rem;
    font-size: 16px;
  }
  .education-text-school {
    font-size: 20px;
    height: 23px;
  }
  .education-text-duration {
    font-size: 16px;
  }
  .education-roundedimg {
    width: 4rem;
    height: 4rem;
    margin-bottom: 0.5rem;
    margin-right: 0px;
  }
  .education-head {
    flex-direction: column; /* Ensure head also stacks */
    align-items: center; /* Center items when stacked */
  }
}

@media (max-width: 768px) {
  .education-card {
    text-align: center;
  }
  .education-card-right {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 100%;
  }
  .education-text-details{
     display: flex;
     flex-direction: column;
     align-items: flex-start;
  }
  .education-card-border {
    width: 100%;
  }
  .education-roundedimg {
    margin-top: 10px;
    width: 4rem;
    height: 4rem;
    margin-bottom: 1rem;
    margin-right: 0;
  }
  .education-text-bullets > ul {
    max-width: 80%;
    margin: unset;
  }
  .education-text-school {
    font-size: 18px; /* Further adjust font size for smaller tablets/large phones */
    height: auto; /* Allow height to adjust if text wraps */
    white-space: normal; /* Allow text to wrap */
    overflow: visible; /* Ensure all text is visible if it wraps */
    text-overflow: clip; /* No ellipsis if text wraps */
  }
  .education-text-subHeader {
    font-size: 15px; /* Further adjust font size */
  }
  .education-text-duration {
    font-size: 15px; /* Further adjust font size */
  }
}

/* --- New Media Query for Mobile Devices (max-width: 480px) --- */
@media (max-width: 480px) {
  .education-card {
    padding-bottom: 5px; /* Reduce padding for very small screens */
  }

  .education-roundedimg {
    width: 3.5rem; /* Slightly smaller image for very small phones */
    height: 3.5rem;
    margin-bottom: 0.8rem; /* Adjust margin */
  }

  .education-text-school {
    font-size: 16px; /* Smaller font size for school name */
  }

  .education-text-subHeader {
    font-size: 14px; /* Smaller font size for sub-header */
    padding-top: 0.3rem; /* Reduce padding */
  }

  .education-text-duration {
    font-size: 14px; /* Smaller font size for duration */
    padding-top: 0.3rem; /* Reduce padding */
  }

  .education-text-desc {
    font-size: 0.9rem; /* Adjust description font size */
    margin-top: 0.3rem;
    margin-bottom: 0.3rem;
  }

  .education-text-bullets > ul {
    font-size: 0.9rem; /* Adjust bullet point font size */
    max-width: 90%; /* Adjust max-width for bullet points */
    margin-top: 0.3rem;
  }

  .education-card-border {
    margin-bottom: 15px; /* Reduce bottom margin for border */
  }
}