// Main.scss

@import "../_globalColor";

/* Universal Box-Sizing and Image Max-Width for Responsiveness */
* {
  box-sizing: border-box;
}

img, video, canvas, svg {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Ensure html, body, and #root do NOT have fixed heights or scrollbars */
/* An inner div will be handling the scrolling */
html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden; /* This prevents the main browser scrollbar */
  background-color: transparent;

  &::-webkit-scrollbar {
    width: 0px;
    background: transparent;
  }
  scrollbar-width: none;
  -ms-overflow-style: none;
}

/* This is the outermost div in Main.jsx that will now be scrollable */
.dark-mode, .light-mode-main-container {
  height: 100vh; /* Make this div fill the viewport height */
  overflow-y: auto; /* THIS IS THE KEY: Makes this specific div scrollable */
  position: relative; /* Needed for z-index context with fixed background */
}

/* Your .main class is now *inside* the scrollable container */
.main {
  position: relative;
  z-index: 1; 
  background-color: transparent; 
  min-height: 65vh;
  overflow: visible; 
  box-sizing: border-box;
}

/* Style for the Header component to ensure it has a solid background and is always on top */
.header-div {
  background-color: var(--background-color);
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 10;
}

/* --- Responsive Adjustments using Media Queries --- */
@media (max-width: 1024px) {
  .main {
    min-height: 55vh;
  }
}
@media (max-width: 767px) {
  .main {
    min-height: 45vh;
    margin-top: 5%;
   
  }
}
@media (max-width: 480px) {
  .main {
    min-height: 40vh;
  }
}

@media (max-width: 360px) {
  .main {
    min-height: 35vh;
  }
}
