@import "../../_globalColor"; // Assuming this path is correct and the file exists

/*React-headroom*/
.headroom-wrapper {
  max-height: 100px; // Base max-height for the headroom wrapper
}

/* header */

.dark-menu {
  background-color: rgba(0, 0, 16, 0.5) !important;
  color: $textColorDark;
  z-index: 5;
}

.dark-menu li a {
  color: $textColorDark !important;
}

.dark-menu li a:hover {
  background-color: $buttonColor !important;
}

.header {
  background-color: rgba(255, 255, 255, 0.3);
  max-width: 100%;
  margin: 0px auto;
  padding: 0 20px; // Base padding for larger screens
}

.header ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
}

.grey-color {
  color: $subTitle;
}

.header li a {
  display: block;
  padding: 15px 20px; // Base padding for menu items
  color: $textColor;
  text-decoration: none;
}

.header li a:hover,
.header .menu-btn:hover {
  background-color: $headerHoverBG;
}

.header .logo {
  display: block;
  flex: none;
  float: left;
  font-size: 1.5em; // Base font size for logo
  text-decoration: none;
  margin-top: 10px; // Base margin-top for logo
  line-height: normal;
}

.header .logo-name {
  font-family: "Agustina Regular";
  font-weight: bold;
  font-variant-ligatures: no-common-ligatures;
  -webkit-font-variant-ligatures: no-common-ligatures;
  padding: 0 10px;
}

/* menu */

.header .menu {
  clear: both;
  max-height: 0;
  transition: max-height 0.2s ease-out;
}

/* menu icon */

.header .menu-icon {
  cursor: pointer;
  display: inline-block;
  float: right;
  padding: 28px 20px; // Base padding for menu icon
  position: relative;
  user-select: none;
}

.header .menu-icon .navicon {
  background: $iconBackground;
  display: block;
  height: 2px;
  position: relative;
  transition: background 0.2s ease-out;
  width: 18px;
}

.header .menu-icon .navicon-dark {
  background: $textColorDark;
}

.header .menu-icon .navicon:before,
.header .menu-icon .navicon:after {
  background: $iconBackground;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  transition: all 0.2s ease-out;
  width: 100%;
}

.header .menu-icon .navicon-dark:before,
.header .menu-icon .navicon-dark:after {
  background: $textColorDark !important;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  transition: all 0.2s ease-out;
  width: 100%;
}

.header .menu-icon .navicon:before {
  top: 5px;
}

.header .menu-icon .navicon:after {
  top: -5px;
}

/* menu btn */

.header .menu-btn {
  display: none;
}

.header .menu-btn:checked ~ .menu {
  max-height: 486px; // Base max-height for expanded mobile menu
}

.header .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent !important;
}

.header .menu-btn:checked ~ .menu-icon .navicon:before {
  transform: rotate(-45deg);
}

.header .menu-btn:checked ~ .menu-icon .navicon:after {
  transform: rotate(45deg);
}

.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
  top: 0;
}

/* --- Existing Desktop Media Query (UNCHANGED) --- */
@media (min-width: 48em) { /* 48em = 768px (assuming base 16px font) */
  .header li {
    float: left;
  }
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px; /* Ensure padding is consistent with base for desktop */
  }
  .header .menu {
    max-height: none; /* Menu is always visible on desktop */
  }
  .header .menu-icon {
    display: none; /* Hide hamburger icon on desktop */
  }
}

/* --- Responsive Adjustments using Media Queries (New Additions) --- */

// Tablet and smaller desktop screens (max-width: 1023px)
// This applies to devices where the mobile menu (hamburger) is active.
@media (max-width: 1023px) {
  .header {
    padding: 0 15px; // Adjust horizontal padding for the header
  }

  .header .logo {
    font-size: 1.3em; // Slightly smaller logo for tablets
    margin-top: 15px; // Adjust vertical alignment
  }

  .header .menu-icon {
    padding: 20px 15px; // Adjust padding for the hamburger icon
  }

  .header li a {
    padding: 12px 15px; // Adjust padding for menu items in expanded mobile menu
    font-size: 0.95rem; // Slightly smaller font size for menu items
  }

  // The headroom-wrapper max-height might need adjustment if the header content changes significantly.
  // For now, 100px is generally fine, but could be reduced if needed.
  .headroom-wrapper {
    max-height: 90px;
  }
}

// Mobile devices (max-width: 767px)
@media (max-width: 767px) {
  .header {
    padding: 0 10px; // Further reduce horizontal padding
  }

  .header .logo {
    font-size: 1.2em; // Smaller logo for phones
    margin-top: 12px; // Adjust vertical alignment
  }

  .header .menu-icon {
    padding: 18px 10px; // Adjust padding for hamburger icon
  }
  .header-main {
  display: flex;
  align-items: center;
  justify-content: space-between;
}


  .header li a {
    padding: 10px 10px; // Further adjust padding for menu items
    font-size: 0.9rem; // Smaller font size for menu items
  }

  .header .menu-btn:checked ~ .menu {
    max-height: 350px; // Adjust max-height for mobile menu to prevent overflow on phones
  }

  .headroom-wrapper {
    max-height: 80px; // Further reduce headroom max-height
  }
}

// Very small mobile devices (max-width: 480px)
@media (max-width: 480px) {
  .header .logo {
    font-size: 1.1em; // Even smaller logo
    margin-top: 10px;
  }

  .header .menu-icon {
    padding: 15px 8px; // Even smaller padding for icon
  }

  .header li a {
    padding: 8px 8px; // Even smaller padding for menu items
    font-size: 0.85rem; // Even smaller font size
  }

  .header .menu-btn:checked ~ .menu {
    max-height: 300px; // Further adjust max-height for very small screens
  }

  .headroom-wrapper {
    max-height: 70px; // Smallest headroom max-height
  }
}

