.textarea-container {
  position: relative;
  width: 100%;
  max-width: 500px; // Base max-width for larger screens
  height: auto; // Let height be determined by content initially, or by animations
  padding-bottom: 200px; // Base padding-bottom to make space for content below
  margin: 0 auto; // Centers it on all screens
}

#animated-textarea {
  width: 100%;
  height: 100%; // Takes full height of its container
  padding: 1rem;
  padding-bottom: 20%; // Base padding-bottom to prevent text from going under button
  box-sizing: border-box;
  background-color: transparent;
  color: #E5E7EB;
  font-size: 1rem; // Base font size
  line-height: 1.6;
  border-radius: 12px;
  border: 2px solid transparent;
  resize: none;
  font-family: inherit;
  transition: box-shadow 0.2s ease-out;
  z-index: 10;
  position: relative;

  overflow: hidden; // Hides horizontal scrollbar
  overflow-y: hidden; // Hides vertical scrollbar
  &::-webkit-scrollbar {
    display: none; // For Webkit browsers
  }
  scrollbar-width: none; // For Firefox
  -ms-overflow-style: none; // For Edge/IE

  text-align: left;
  vertical-align: top;

  &:focus {
    outline: none;
  }
  &::placeholder {
    color: #9CA3AF;
    vertical-align: top;
    text-align: left;
  }
}

#border-svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 5;
}

#wavy-border-path {
  fill: #38BDF8;
  fill-opacity: 0;
}

#action-button {
  position: absolute;
  bottom: 1rem; // Base bottom position for consistency with responsive adjustments
  right: 1rem; // Base right position
  padding: 0.37rem 1rem; // Base padding
  background-color: transparent;
  border: none;
  color: white;
  font-weight: 500;
  font-size: 0.9rem; // Base font size
  cursor: pointer;
  z-index: 11;
  opacity: 0;
}

/* --- Responsive Adjustments using Media Queries (Refined) --- */

// Tablet and larger phone devices (max-width: 768px)
@media (max-width: 768px) {
  .textarea-container {
    max-width: 90%; // Make container take up more width on tablets
    height: 180px; // Fixed height for this breakpoint
    padding-bottom: 150px; // Adjust padding-bottom for animations
  }

  #animated-textarea {
    font-size: 0.9rem; // Slightly smaller font size
    padding: 0.8rem; // Reduced padding
    padding-bottom: 25%; // Adjust padding-bottom for button positioning
  }

  #action-button {
    padding: 0.5rem 0.9rem; // Adjust padding
    font-size: 0.8rem; // Smaller font size
    bottom: 0.8rem; // Adjust button position
    right: 0.8rem; // Adjust button position
  }
}

// Mobile devices (max-width: 480px)
@media (max-width: 480px) {
  .textarea-container {
    max-width: 95%; // Take up even more width on smaller phones
    height: 160px; // Further reduced fixed height
    padding-bottom: 130px; // Further adjust padding-bottom
  }

  #animated-textarea {
    font-size: 0.85rem; // Even smaller font size
    padding: 0.7rem; // Further reduced padding
    padding-bottom: 30%; // Further adjust padding-bottom
  }

  #action-button {
    padding: 0.4rem 0.8rem; // Further reduced padding
    font-size: 0.75rem; // Even smaller font size
    bottom: 0.7rem; // Further adjust button position
    right: 0.7rem; // Further adjust button position
  }
}

// Very small mobile devices (max-width: 375px) - Optional, for extra fine-tuning
@media (max-width: 375px) {
  .textarea-container {
    height: 140px; // Even smaller fixed height for very small screens
    padding-bottom: 110px; // Adjust padding-bottom
  }

  #animated-textarea {
    font-size: 0.8rem; // Smallest font size
    padding: 0.6rem; // Smallest padding
    padding-bottom: 35%; // Adjust padding-bottom
  }

  #action-button {
    padding: 0.3rem 0.6rem; // Smallest padding
    font-size: 0.7rem; // Smallest font size
    bottom: 0.6rem; // Smallest button position
    right: 0.6rem; // Smallest button position
  }
}